<template>
  <div>
    <div id="mini" class="container">
      <div class="card">
        <div class="card-header">
          Switch to desktop
        </div>
        <div class="card-body">
          Metamask mobile version is not yet battletested, thus not yet supported by us.
        </div>
      </div>
    </div>
    <div class="grand-text" id="with-bg-op">
      <div id="first-prec" class="row">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name: "MainContainer"
}
</script>

<style scoped>
@media only screen and (min-width: 750px) {
  #with-bg-op {
    display: block;
  }
}

@media only screen and (max-width: 750px) {
  #with-bg-op {
    display: none;
  }
}

@media only screen and (min-width: 750px) {
  #mini {
    display: none;
  }
}

@media only screen and (max-width: 750px) {
  #mini {
    display: block;
    padding: 50px;
  }
}

#with-bg-op {
  position: relative;
  background-color: gold;
}

#with-bg-op:after {
  content : "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../../../static/stadium.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  opacity : 0.25;
  z-index: 10;
}

#first-prec {
  position: relative;
  z-index: 20;
}

.grand-text {
  height: 80vh;
}
</style>



