<template>
  <div id="root">
    <ModalHeader :title="'Oddses - explained'"></ModalHeader>
    <div class="math">
      <p>If 100 eth is placed on team 1, and 100 eth is placed on team 2, in any case, every winner doubles up.</p>
      <p>If 100 eth is placed on team and 200 eth is placed on team 2:</p>
      <ul>
        <li>If team 1 wins, the winners triple up.</li>
        <li>If team 2 wins, the winners win 1.5x their stake.</li>
      </ul>
      <p>If 100 eth is placed on team and 300 eth is placed on team 2:</p>
      <ul>
        <li>If team 1 wins, the winners get 4x.</li>
        <li>If team 2 wins, the winners win 1.33x their stake.</li>
      </ul>
      <p>And so on...</p>
      <hr>
      <p>
        That is exactly the same logic bookmakers apply. However... when a player SHOULD double up, he gets around 1.85x.
        The "rake" that the house wins is almost 10 percent.
        Based on the incorruptable smart contract we have deployed, we take around ~1 percent only.
        Bookmakers "guess" the oddses beforehand. They are actually pretty good at it. We show your their numbers - plus a little.
        The contract that guarantees this is visible to anyone:<br>
        <span class="a">https://etherscan.io/address/0xf93dfc6c1bc46615d3d51d29cae87557a9f06a01</span>
      </p>
    </div>
  </div>
</template>

<script>
import ModalHeader from '../../../common/ModalHeader'

export default {
  name: 'Math',
  data() {
    return {

    }
  },
  props: {

  },
  methods: {
  },
  components: {
    ModalHeader
  },
  computed: {

  },
  created() {
  }
}
</script>

<style scoped>
#root {
  position: relative;
}

.a {
  cursor: pointer;
  text-decoration: underline;
  font-style: italic;
  color: green;
}

.math {
  font-size: 0.8rem;
}
</style>



