<template lang="html">
  <div id="root">
    <ModalHeader :title="'Bet history'"></ModalHeader>
    <div class="body">
      <div class="card">
        <div class="card-header">
          My completed (mined) bets
        </div>
        <div class="card-body  my-bets">
          <div><span>My bet on France:</span><span>{{ homeBet }}&nbsp;eth</span></div>
          <hr>
          <div><span>My bet on England:</span><span>{{ awayBet }}&nbsp;eth</span></div>
        </div>
      </div>
      <p id="hint">
        Transactions take time to complete. Blocks are mined on the blockchain in every ~17 seconds.
        Once a bet is pending, it is being mined. It might take a few blocks to complete - you can leave the site
        however, it wont break the process.
      </p>
    </div>
    <hr>
    <div class="withdraw">
      <Withdraw></Withdraw>
    </div>
  </div>
</template>

<script>
import ModalHeader from '../../../common/ModalHeader'
import Withdraw from './Withdraw'

export default {
  name: "BetHistory",
  data() {
    return {
      homeBet: null,
      awayBet: null
    }
  },
  methods: {
    update: function() {
      this.$match.getMyBetHistory()
      .then(bet => {
        this.homeBet = parseFloat(web3.fromWei(bet[0], "ether"))
        this.awayBet = parseFloat(web3.fromWei(bet[1], "ether"))
      })
    }
  },
  components: {
    ModalHeader,
    Withdraw
  },
  created() {
    this.update()
    this.$match.listenToBetReport(this.update)
  }
}
</script>

<style lang="css" scoped>
#root {
  position: relative;
}

#hint {
  font-size: 0.8rem;
  font-style: italic;
  margin: 20px;
  color: #555;
  text-align: center;
}

.body {
  text-align: justify;
}

.card {
  margin: 30px;
}

.my-bets div {
  display: flex;
  justify-content: space-between;
}

.withdraw {
  text-align: right;
}
</style>



