<template>
  <div id="root">
    <MainContainer>
      <Account></Account>
      <div class="container" id="middle">
        <div class="row">
          <div class="col-md-4 col-sm-6">
            <div class="row justify-content-center">
              <div class="col-md-12 col-lg-7">
                <div class="my-card">
                  <div class="my-header">
                    <span class="odds" @click="$openApp('Math')">~1.65x</span>
                  </div>
                  <div class="my-body">
                    <Flag :flags="['france']" :flow="'up'"></Flag>
                    <BetButton :bool="true"></BetButton>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="col-md-4 d-none d-md-block">
            <div class="row justify-content-center">
              <div class="col-md-9">
                <img src="../../../../../static/favicon.png" id="worldcupimage">
              </div>
            </div>
          </div>

          <div class="col-md-4 col-sm-6">
            <div class="row justify-content-center">
              <div class="col-md-12 col-lg-7">
                <div class="my-card">
                  <div class="my-header">
                    <span class="odds" @click="$openApp('Math')">~2.85x</span>
                  </div>
                  <div class="my-body">
                    <Flag :flags="['croatia']" :flow="'down'"></Flag>
                    <BetButton :bool="false"></BetButton>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Countdown v-if="$appStatus('beforeBet')"></Countdown>
    </MainContainer>

    <ShortText
      @readMore="slide()"
      :firstLine="'Use your ether to bet on the world cup final here, with the best possible oddses, with any amount you wish to, with just one click.'"
      :secondLine="'This bet lives on an incorruptable smart contract on the ethereum blockchain transparently and anonymously.'"
      :thirdLine="'Play now, or collect some more info before that.'">
    </ShortText>

    <Info></Info>

    <Footer></Footer>
  </div>
</template>

<script>
import MainContainer from '../../../common/MainContainer'
import ShortText from '../../../common/ShortText'
import Footer from '../../../common/Footer'
import Flag from './Flag'
import Info from './Info'
import BetButton from './BetButton'
import Account from './Account'
import Countdown from './Countdown'

export default {
  name: 'LandingPage',
  components: {
    MainContainer,
    ShortText,
    Info,
    Flag,
    BetButton,
    Account,
    Countdown,
    Footer
  },
  data() {
    return {
      homePlayed: false,
      awayPlayed: false
    }
  },
  methods: {
    slide: function () {
      window.scrollTo({top: 650, behavior: "smooth"})
    }
  }
}
</script>

<style scoped>
@media only screen and (min-height: 700px) {
  #middle {
    margin-top: 120px;
  }
}

@media only screen and (max-height: 700px) {
  #middle {
    margin-top: 90px;
  }
}

@media only screen and (max-height: 600px) {
  #middle {
    margin-top: 60px;
  }
}

@media only screen and (max-height: 500px) {
  #middle {
    margin-top: 30px;
  }
}

@media only screen and (max-height: 400px) {
  #middle {
    margin-top: 10px;
  }
}

@media only screen and (min-width: 900px) {
  #worldcupimage {
    max-width: 200px;
  }
}

@media only screen and (max-width: 900px) {
  #worldcupimage {
    max-width: 150px;
  }
}

@media only screen and (max-width: 850px) {
  #worldcupimage {
    max-width: 120px;
  }
}

@media only screen and (max-width: 800px) {
  #worldcupimage {
    max-width: 100px;
  }
}

.col-md-12 {
  font-size: 0.8rem;
}

.my-header {
  display: flex;
  justify-content: space-around;
  color: black;
  width: 100%;
  padding: 10px;
  padding-bottom: 0;
}

.my-body {
  padding: 10px;
  padding-top: 0;
}

.odds {
  margin: 0;
  padding: 5px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 3px;
  background-color: gold;
  font-weight: bold;
  cursor: pointer;
  box-shadow: inset 0 0 5px #555;
}

.odds:hover {
  text-decoration: underline;
}

.my-card {
  padding: 0;
  background-color: rgba(255,215,0,0.4);
  border-radius: 3px;
  box-shadow: 0 0 15px #555;
  transition: 0.7s;
}

.my-card:hover {
  background-color: rgba(255,215,0,0.85);
}

.my-card:hover {
  opacity: 1;
}

.hoverable {
  opacity: 0.7;
}

.hoverable:hover {
  opacity: 1;
}
</style>



